import { openAuth, showToast } from 'src/utils/nativeCommunicator';

interface Props {
  failAuthFn?: () => void;
  successAuthFn?: () => void;
}

interface ReturnUseAuthorize {
  requestAuth: () => void;
}

const useAuthorize = ({
  successAuthFn,
  failAuthFn,
}: Props): ReturnUseAuthorize => {
  const handleSuccess = () => {
    successAuthFn?.();
  };

  const handleFail = () => {
    failAuthFn
      ? failAuthFn()
      : showToast(`인증이 실패하였습니다. 잠시 후 다시 시도해주세요.`);
  };

  const requestAuth = () => {
    /** openAuth 네이티브 동작 params */
    const { callbackId, onSuccess, onFail } = {
      callbackId: `openAuth-${new Date().getTime()}`,
      onSuccess: handleSuccess,
      onFail: handleFail,
    };

    openAuth(callbackId, onSuccess, onFail);
  };

  return {
    requestAuth,
  };
};

export default useAuthorize;
