import { shareLink, showToast } from 'src/utils/nativeCommunicator';

interface RequestShareLink {
  title: string;
  text: string;
  url: string;
}

interface Props {
  failFn?: () => void;
  successFn?: () => void;
}

interface ReturnUseShareLink {
  requestShareLink: (props: RequestShareLink) => void;
}

const useShareLink = ({ failFn, successFn }: Props): ReturnUseShareLink => {
  const handleSuccess = () => {
    successFn ? successFn() : showToast(`공유에 성공하였습니다.`);
  };

  const handleFail = () => {
    failFn
      ? failFn()
      : showToast(`공유에 실패하였습니다. 잠시 후 다시 시도해주세요.`);
  };

  const requestShareLink = ({ title, text, url }: RequestShareLink) => {
    console.log('requestShareLink', 'title', title);
    console.log('requestShareLink', 'text', text);
    console.log('requestShareLink', 'url', url);
    const { callbackId, onSuccess, onFail } = {
      callbackId: `openAuth-${new Date().getTime()}`,
      onSuccess: handleSuccess,
      onFail: handleFail,
    };
    shareLink(title, text, url, callbackId, onSuccess, onFail);
  };

  return {
    requestShareLink,
  };
};

export default useShareLink;
